header {
    background-color: white;
    box-shadow: 0px 3px 10px rgb(203, 203, 203);
    position: sticky;
    top: -10px;
    z-index: 50;
    transition: .3s;
}

header:hover {
    box-shadow: 0px 3px 15px rgb(187, 187, 187);
}

.header {
    display: flex;
    padding-top: 10px;
    justify-content: space-between;
    align-items: center;
}

@media (max-width: 720px) {
    header {
        top: 0;
    }

    .header {
        padding-top: 0px;
    }
}

.logo {
    display: flex;
    flex: 1;
    gap: 17px;
}

@media (max-width: 850px) {
    .logo {
        gap: 5px;
    }
}

@media (max-width: 720px) {
    .logo {
        margin-left: 18px;
    }
}

.menu__box>.logo {
    margin-left: 80px;
}


.nav {
    flex: 2;
    list-style: none;
    display: flex;
    justify-content: space-between;
    gap: 24px;
}

@media (max-width: 850px) {
    .nav {
        gap: 16px;
    }
}

.block__button {
    display: flex;
    flex: 8;
    gap: 18px;
    justify-content: right;
    align-items: center;
}

.brand {
    font-weight: bold;
}

@media (max-width: 850px) {
    .brand {
        font-size: 18px;
    }

    .image {
        max-width: 24px;
    }
}

.item {
    position: relative;
    cursor: pointer;
    font-size: 14px;
}

.item::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    top: 19px;
    background-color: rgb(12, 93, 120);
    transition: 0.3s;
    transform: scaleX(0);
    transform-origin: left;
}

.item:hover::before {
    transform: scaleX(1);
}

.button {
    width: 130px;
    height: 44px;
    background: #5C5CFF;
    border: transparent;
    border-radius: 4px;

    text-align: center;
    letter-spacing: 1.5px;
    text-transform: uppercase;

    color: #FFFFFF;
    transition: 0.3s;
}

.button:hover {
    background: #5656d4;
}

.block_finder {
    position: relative;
}

.find {
    cursor: pointer;
    transition: 1s;
}

@media (max-width: 950px) {
    .find {
        max-width: 24px;
    }
}


.input {
    width: 0px;
    font-size: 20px;
    border: 0;
    border-bottom: 3px solid rgba(184, 184, 184, 0.498);
    outline: none;
    margin: 0px 10px;
    transform: scaleX(0);
    transform-origin: bottom;
    transition: 0.5s;
}

@media(max-width: 950px) {
    .active {
        font-size: 14px;
        border-bottom: 1px solid rgba(184, 184, 184, 0.498);
    }
}

.active {
    transform: scaleX(1);
    width: 200px;
}

@media (max-width: 950px) {
    .active {
        width: 100px;
    }
}

.hamburger_menu {
    top: 0;
    display: none;
}

@media (max-width: 720px) {

    .block__button,
    .nav {
        display: none;
    }

    .hamburger_menu {
        display: block;
    }
}

#menu__toggle {
    opacity: 0;
}

/* стилизуем кнопку */
.menu__btn {
    display: flex;
    /* используем flex для центрирования содержимого */
    align-items: center;
    /* центрируем содержимое кнопки */
    position: fixed;
    top: 10px;
    right: 10px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
}

/* добавляем "гамбургер" */
.menu__btn>span,
.menu__btn>span::before,
.menu__btn>span::after {
    transition: 0.5s;
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #616161;
}

.menu__btn>span::before {
    content: '';
    top: -8px;
}

.menu__btn>span::after {
    content: '';
    top: 8px;
}

.menu__box {
    transition: 0.5s;
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    left: -100%;
    width: 300px;
    height: 100%;
    margin: 0;
    padding: 20px 0 80px 0;
    list-style: none;
    text-align: center;
    background-color: #5656d4;
    box-shadow: 1px 0px 6px rgba(0, 0, 0, .2);
}

/* элементы меню */

.menu__items {
    margin-top: 20px;
}

.menu__item {
    display: block;
    padding: 12px 24px;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    margin: 5px 0px;
    font-weight: 600;
    text-decoration: none;
}

.menu__item:hover {
    background-color: #111131;
}



.focus.menu__btn>span {
    transform: rotate(45deg);
}

.focus.menu__btn>span::before {
    top: 0;
    transform: rotate(0);
}

.focus.menu__btn>span::after {
    top: 0;
    transform: rotate(90deg);
}

.focus~.menu__box {
    visibility: visible;
    left: 0;
}

@media (max-width: 720px) {
    .menu__box>.block__button {
        display: block;
    }

    .menu__box>.block__button>.block_finder>.find {
        display: none;
    }

    .menu__box>.block__button>.block_finder>.input {
        transform: scaleX(1);
        width: 90%;
        background-color: transparent;
        color: white;
        border-bottom: 1px solid white;
        margin: 20px 0px;
    }
}