.block_title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 32px;
    padding-bottom: 19px;
    align-items: center;
    border-bottom: 1px solid #868297;
}

@media (max-width: 1230px) {
    .block_title {
        margin-bottom: 16px;
        padding-bottom: 8px;
        width: 96%;
        margin-left: 2%;
    }
}

.title {
    position: relative;
    font-size: 14px;
    color: #413E52;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 1.5px;
}

.title::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 4px;
    background-color: #413E52;
    bottom: -31px;
    transition: 2s;
}

@media (max-width: 1230px) {
    .title::before {
        bottom: -20px;
    }
}

.button {
    position: relative;
    background-color: transparent;
    border: 0;
    font-size: 14px;
    color: #413E52;
    font-weight: 400;
    letter-spacing: 1.5px;
    margin-right: 25px;
}

.button::after {
    content: url('./Image/Arrow.png');
    position: absolute;
    top: 20%;
    right: -25px;
}