.offer {
    background-color: #111131;
    padding: 36px;
    margin: 128px;
}

@media (max-width: 950px) {
    .offer {
        padding: 32px 24px;
        margin: 24px;
    }
}

@media (max-width: 500px) {
    .offer {
        margin: 8px;
    }
}

.category {
    font-size: 13px;
    color: #5C5CFF;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 1.5px;
}

.content {
    display: flex;
    justify-content: space-between;
}

@media (max-width: 782px) {
    .content{flex-direction: column;}
}

.buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: right;
    position: relative;
    width: 40%;
}

.button {
    position: relative;
    width: 100%;
    max-height: 56px;
    margin-top: 10px;
}

@media (max-width: 850px) {
    .button {
        font-size: 16px;
        padding: 10px;
    }
}

@media (max-width: 782px) {
    .buttons {
        width: 100%;
    }
    .button {
        width: 100%;
        max-width: 100%;
    }
}

.title {
    color: white;
    font-size: 31px;
    font-weight: 400;
    max-width: 500px;
    letter-spacing: 1px;
}
@media (max-width:1150px) {
    .title {
        font-size: 25px;
    }
}

@media (max-width: 850px) {
    .title {
        font-size: 18px;
    }
}

@media (max-width: 500px) {
    .title {
        letter-spacing: 0;
        font-size: 16px;
    }
}