body {
  overflow-x: hidden;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
}

.container1220 {
  max-width: 1220px;
  margin: 0 auto;
}

button {
  cursor: pointer;
}

.primary {
  position: relative;
  font-size: 20px;
  color: #5C5CFF;
  background-color: transparent;
  border: 1px solid #8989F8;
  z-index: 1;
  transition: 0.5s;
}

.primary::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #8989F8;
  transform: scaleY(0);
  transition: 0.5s;
  transform-origin: bottom;
  z-index: 0;
}

.primary:hover {
  color: white;
}

.primary:hover::before {
  transform: scaleY(1);
  z-index: -2;
}

.secondary {
  color: white;
  background-color: #5C5CFF;
  border: 1px solid transparent;
  letter-spacing: 1.5px;
  transition: 0.3s;
}

.secondary:hover {
  background-color: #5656d4;
}

.author {
  font-size: 13px;
  color: #868297;
  font-weight: 400;
}
