@font-face {
  font-family: "Inter";
  src: url("./fonts/Inter-Regular.ttf");
  font-weight: normal;
}

@font-face {
  font-family: "Inter";
  src: url("./fonts/Inter-Bold.ttf");
  font-weight: bold;
}

@font-face {
  font-family: "Inter";
  src: url("./fonts/Inter-Light.ttf");
  font-weight: lighter;
}

body {
  font-family: 'Inter';
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
