.container {
    width: 450px;
    margin: 0 auto;
}

.alert {
    background-color: #111131;
    color: #ABABFA;
    padding: 10px 0px;
    font-size: 13px;
    display: flex;
}

.closed {
    display: none;
}

.container > a {
    margin-left: 30px;
}

.container > a:hover {
    text-decoration: underline;
}

.close {
    margin-right: 30px;
    cursor: pointer;
}

@media (max-width: 550px) {
    .alert {
        display: none;
    }   
}