.title {
    font-size: 29px;
    font-weight: 400;
    color: #413E52;
}

.author {
    margin: 0;
}

.text {
    font-size: 20px;
    font-weight: 400;
    margin: 7px 0px;
    line-height: 30px;
}


@media (max-width:772px) {
    .text {
        font-size: 16px;
        line-height: 20px;
    }
}

@media (max-width:500px) {
    .text {
        font-size: 12px;
        line-height: 15px;
        margin-top: 2px;
    }
}

.comment {
    margin-bottom: 30px;
}

@media (max-width:1220px) {
    .comment {
        font-size: 21px;
        max-width: 500px;
    }
}

@media (max-width:772px) {
    .comment {
        max-width: 300px;
        margin-bottom: 15px;
    }
}

@media (max-width:500px) {
    .comment {
        font-size: 16px;
    }
}


@media (max-width:380px) {
    .comment {
        max-width: 96%;
        margin-left: 2%;
    }
}


@media (max-width: 380px) {
    .author {
        font-size: 16px;
    }   
    .text {
        font-size: 18px;
        line-height: 24px;
        margin-top: 2px;
    } 
}

