.article {
    max-width: 365px;
    cursor: pointer;
    transition: 0.3s;
}

@media (max-width: 1230px) {
    .article {max-width: 320px;}
}

@media (max-width: 992px) {
    .article {max-width: 260px;}
}

@media (max-width: 800px) {
    .article {max-width: 240px;}
}

@media (max-width: 720px) {
    .article {
        max-width: 95%;
        display: flex;
        margin-left: 2.5%;
    }
}

.article:hover {
    box-shadow: 0px 3px 15px rgb(187, 187, 187);
}

.list {
    display: flex;
    justify-content: space-between;
}

@media (max-width: 1230px) {
    .list{justify-content: space-around;}
}

@media (max-width: 720px) {
    .list {
        display: flex;
        flex-direction: column; 
        gap: 15px;
        justify-content: center;
    }
    
}
.content {
    border: 1px solid #EBEAF2;
    border-top: 0;
    padding:  24px 37px;
    margin-top: -5px;
}

@media(max-width: 720px) {
    .content {
        padding: 0px 9px;
        width: 100%;
    }
}

.image {
    max-width: 365px;
}

@media (max-width: 1230px) {
    .image {max-width: 320px;}
}

@media (max-width: 992px) {
    .image {max-width: 260px;}
}

@media (max-width: 800px) {
    .image {max-width: 240px;}
}

@media (max-width: 720px) {
    .image {
        max-width: 180px;
        height: 180px;
    }
}

.category {
    font-size: 14px;
    color: #5C5CFF;
    font-weight: 400;
    letter-spacing: 1px;
    text-transform: uppercase;
}

@media(max-width: 720px) {
    .category {
        font-size: 12px;
        letter-spacing: 0.5px;
    }
}

.author {
    font-size: 13px;
    color: #868297;
    font-weight: 400;
}

.article_title {
    color: #413E52;
    font-size: 19px;
    font-weight: 400;
}

@media(max-width: 720px) {
    .article_title {
        font-size: 15px;
    }
}

@media(max-width: 350px) {
    .article_title {
        font-size: 12px;
    }
}