.footer {
    padding: 120px 0px;
    background-color: #1B1B4D;
}

@media (max-width: 772px) {
    .footer {padding: 70px 0px;}
}


@media (max-width: 420px) {
    .footer {padding: 50px 0px;}
}


.desc_flex {
    display: flex;
}

@media (max-width: 1050px) {
    .desc_flex {
        display: block;
    }
}

.block {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

@media (max-width: 772px) {
    .block {
        flex-direction: column;
    }
}

@media (max-width: 420px) {

}

a {
    text-decoration: none;
    color: #ABABFA;
    transition: 0.2s;
}

ul {
    list-style: none;
    font-weight: normal;
}

.brands {
    margin-top: 20px;
    display: flex;
    gap: 12px;
    flex: 1;
}

@media (max-width: 1320px) {
    .brands {gap: 5px;}
}

@media (max-width: 1050px) {
    .brands {margin-top: 0px;}
}

@media (max-width: 420px) {
    .brands {
        gap: 10px;
        margin-top: 10px;
    }
}
.lists {
    display: flex;
    flex: 2;
    justify-content: left;
    gap: 15%;
}

@media (max-width: 1320px) {
    .lists{
        flex: 1;
        gap: 5%;
    }
}

@media (max-width: 1050px) {
    .lists {
        gap: 0%;
        margin-top: 25px;
    }
    .logo {
        margin-left: 35px;
    }
}

@media (max-width: 772px) {
    .logo {
        margin-left: 0;
    }
    .lists {
        gap: 100px;
    }
    .list {
        padding-left: 0;
    }
}

@media (max-width: 420px) {
    .lists {
        gap: 0px;
        flex-direction: column;
    }
}


.news {
    flex: 1;
    max-width: 380px;
}

.brand {
    background-color: #ABABFA;
    padding: 10px;
    border-radius: 50%;
}

.title {
    font-weight: 400;
    color: #ABABFA;
    margin-top: 0;
    text-transform: uppercase;
}


.item {
    margin: 20px 0px;
    letter-spacing: 1px;
}

.item:hover a {
    color: white;
}

.desc {
    font-size: 30px;
    font-weight: 400;
    color: white;
    margin: 5px 0px;
}

@media (max-width: 420px) {
    .desc {
        font-size: 24px;
    }
}

.buttons {
    margin-top: 20px;
}

.button {
    width: 100%;
    padding: 15px 0px;
    font-size: 20px;
    margin: 5px 0px;
    max-height: 55px;
    border-radius: 4px;
}

@media (max-width: 1050px) {
    .button {
        width: 70%;
    }
}

@media (max-width: 420px) {
    .button {
        width: 100%;
    }
}
.info {
    background-color: #ABABFA;
    padding: 15px 0px;
}

.info_item {
    margin-left: 30px;
}

@media (max-width: 750px) {
    .info_item {
        margin-left: 10px;
    }
}

@media (max-width: 690px) {
    .info_item {
        margin-left: 0;
    }
    .information {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
}