.block {
    display: flex;
    position: relative;
    justify-content: space-between;
    margin: 120px auto;
    border: 1px solid #EBEAF2;
    border-radius: 4px;
    z-index: 1;
    background-color: white;
    transition: .3s;
}

@media (max-width: 1220px) {
    .block {
        max-width: 1000px;
    }
}

@media (max-width: 1000px) {
    .block {
        margin: 80px auto;
        max-width: 800px;
    }
}



@media (max-width: 800px) {
    .block {
        margin: 50px auto;
        max-width: 500px;
    }
}


@media (max-width: 500px) {
    .block {
        flex-direction: column;
        margin: 20px auto;
        max-width: 96%;
        margin-left: 2%;
    }
}



.block:hover {
    box-shadow: 0px 3px 10px rgb(203, 203, 203);
}

.arrows {
    position: absolute;
    z-index: 0;
}

.arrows_left {
    top: 440px;
    left: 90px;
}

.arrows_right {
    top: 170px;
    right: 90px;
}

@media (max-width:1220px) {
    .arrows_left {
        left: 0;
    }

    .arrows_right {
        right: 0;
    }
}

@media (max-width:1000px) {
    .arrows_left, .arrows_right {
        max-width: 250px;
    }

    .arrows_left {
        left: 0;
    }

    .arrows_right {
        right: 0;
    }
}

@media (max-width:800px) {
    .arrows_left, .arrows_right {
        max-width: 200px;
    }

    .arrows_left {
        top: 20px;
        left: 0;
    }

    .arrows_right {
        top: 180px;
        right: 0;
    }
}

@media (max-width:500px) {
    .arrows_left, .arrows_right {
        display: none;
    }
}

.text {
    padding: 65px 48px;
}

@media (max-width: 1220px) {
    .text{padding: 45px 24px;}
}

@media (max-width: 800px) {
    .text{padding: 10px 15px;}
}

@media (max-width: 500px) {
    .text{order: 2;}
}



.background {
    max-width: 600px;
    max-height: 560px;
}


@media (max-width:1000px) {
    .background {
    max-width: 50%;
    max-height: 100%;
    }
}

@media (max-width:500px) {
    .background {
        max-width: 100%;
        max-height: 100%;
        order: 1;
    }
}

.user,
.category {
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.category {
    color: #5C5CFF;
}

@media (max-width: 800px) {
    .category, .user {font-size: 10px;}
    .user {margin: 5px 0px;}
}


@media (max-width: 500px) {
    .category, .user {font-size: 16px;}
    .user {margin: 5px 0px;}
}


.title {
    max-width: 460px;
    font-size: 42px;
    font-weight: 400;
    letter-spacing: 1.5px;
}

@media (max-width: 1220px) {
    .title {
        font-size: 28px;
    }
}

@media (max-width: 800px) {
    .title {
        font-size: 18px;
        margin: 15px 0px;
    }
}

@media (max-width: 500px) {
    .title {
        font-size: 24px;
        margin: 10px 0px;
        line-height: 1.1;
        letter-spacing: 0.6px;
    }
}

.date {
    font-size: 13px;
    color: #868297;
}

@media (max-width: 500px) {
    .date{font-size: 10px; margin: 2px 0px;}
}