.heading {
    display: flex;
    gap: 55px;
    margin-bottom: 220px;
}

@media (max-width: 860px) {
    .heading {

        margin: 50px 0px;
    }
}

@media (max-width: 860px) {
    .heading {
        flex-direction: column;
    }
}

.comments {
    max-width: 370px;
}

@media (max-width: 860px) {
    .comments {
        max-width: 100%;
        order: 2;
    }
}

.content {
    width: 370px;
    border-radius: 8px;
}

@media (max-width: 860px) {
    .content {
        order: 1;
        max-width: 98%;
        margin-left: 1%;
    }
}

.content {
    background-color: #111131;
    text-align: center;
    padding: 48px 0px;
}

.titles {
    margin-top: 40px;
    margin-bottom: 0;
}

@media (max-width: 1220px) {
    .titles {
        margin-top: 10px;
    }
}

.title {
    color: #5C5CFF;
    font-size: 14px;
    font-weight: 600;
    margin: 0;
    letter-spacing: 2px;
    text-transform: uppercase;
}

.desc {
    color: white;
    font-size: 23px;
    font-weight: 400;
    padding: 0 50px;
}

@media (max-width: 1220px) {
    .desc {
        font-size: 16px;
    }
}

.buttons {
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    width: 270px;
    gap: 15px;
}

@media (max-width: 1220px) {
    .buttons {
        width: 200px;
    }
}

.button {
    width: 100%;
    font-size: 20px;
    padding: 13px 0px;
}

@media (max-width: 722px) {
    .button {
        font-size: 16px;
        padding: 6px 0px;
    }
}
