.case {
    display: flex;
    justify-content: space-between;
    gap: 55px;
    position: relative;
}


@media (max-width: 1230px) {
    .case {
        justify-content: center;
        gap: 30px;
    }
}

@media (max-width: 772px) {
    .case {
        gap: 25px;
    }
}


@media (max-width: 500px) {
    .case {
        gap: 5px;
    }
}

@media (max-width: 380px) {
    .case {
        gap: 20px;
        flex-direction: column;
    }
}

.block {  
    background-color: white;
    border: 1px solid #8682975f;
    max-width: 696px;
    margin-bottom: 120px;
    border-radius: 0 0 8px 8px;
}

.content {
    padding: 11px 24px;
}



.image {
    max-width: 100%;
}

@media (max-width: 1230px) {

    .block {
        max-width: 500px;
    }

    .image {
        max-width: 500px;
    }
}

@media (max-width: 772px) {
    .block {
        max-width: 300px;
    }

    .image {
        max-width: 300px;
    }
}

@media (max-width: 500px) {
    .block {
        max-width: 200px;
    }

    .image {
        max-width: 200px;
    }
}


@media (max-width: 380px) {
    .block {
        max-width: 98%;
        margin-left: 0.5%;
        margin-bottom: 10px;
    }

    .image {
        max-width: 100%;
    }
}


.arrows {
    position: absolute;
    top: 50px;
    left: -55px;
    z-index: -2;
}

@media(max-width: 1230px) {
    .arrows {
        display: none;
    }
}

@media(max-width: 772px) {
    .title {
        font-size: 24px;
    }
}

@media(max-width: 772px) {
    .title {
        font-size: 18px;
    }
}